
  
  .skills {
    background-color: #51C6DA;
    color: white;
    height: 100%;

    &__content-wrapper {
      display: flex;
      padding: 20px;
      gap: 20px;
//       width: 60%;
// margin: 0,auto;
// justify-content: center;
// align-items: center;
  
      &__inner-content {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
  
        &__category-text {
          display: inline-block;
          font-size: 22px;
          font-weight: 700;
          line-height: 1rem;
          letter-spacing: 0.5px;
          position: relative;
          text-transform: uppercase;
          padding: 5px 10px;
          border-bottom: 2px solid var(--yellow-theme-main-color);
        }
  
        &__category-text::before,
        &__category-text::after {
          position: absolute;
          content: '';
          width: 2px;
          height: 5px;
          background-color: var(--yellow-theme-main-color);
          bottom: -1px;
        }
  
        &__category-text::before {
          left: 0;
        }
  
        &__category-text::after {
          right: 0;
        }
  
        &__progressbar-container {
          width: 100%;
        }
  
        .progressbar-wrapper {
          padding: 50px 0px 0px 0px;
  
          p {
            font-size: 1.6rem;
            margin: 0 0 10px;
            font-weight: 500;
            text-transform: uppercase;
            color: var(--yellow-theme-sub-text-color);
          }
        }
      }
    }
  }

  .progress-bar {
    width: 100%;
    height: 10px;
    background-color: #f0f0f0; // Background color of the progress bar
    border-radius: 5px;
    overflow: hidden; // Ensures the filling bar doesn't overflow
  }
  
  .filling-bar {
    height: 100%;
    background-color: var(--yellow-theme-main-color); // Color of the filling bar
    border-radius: 5px;
    transition: width 5s ease-in-out; // Smooth transition for width change
  }
  
  
  
  @media only screen and (max-width: 768px) {
    .skills {
      &__content-wrapper {
        flex-direction: column;
      }
    }
  
    .skills__content-wrapper__inner-content__progressbar-container {
      margin-bottom: 20px;
    }
  }
  